export async function POST(req: Request){
    try{const data = await req.json();
    const { id } = data;
    return Response.json({message:`${id} oiii`,status:200});
    } catch(error) {
        return Response.json({message: `${error}`, status: 500});
    }
}
export async function GET(req: Request){
    try{const data = await req.json();
    const { id } = data;
    return Response.json({message:`${id} oiii`,status:200});
    } catch(error) {
        return Response.json({message: `${error}`, status: 500});
    }
}